.slideout-menu {
  padding: 10px;
 position: fixed;
 top: 0;
 bottom: 0;
 width: 256px;
 height: 100vh;
 overflow-y: scroll;
 -webkit-overflow-scrolling: touch;
 z-index: 999;
 display: none;
 background-color: $brand-primary;
 color: $white;
 a {
   color:$white;
 }

}

.slideout-menu-left {
 left: 0;
}

.slideout-menu-right {
 right: 0;
}

.slideout-panel {

 position: relative;
 z-index: 1;
 will-change: transform;
 min-height: 100vh;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
 overflow: hidden;
}

.slideout-open .slideout-menu {
 display: block;
}
