/**
 *  MAIN RULES
 */

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: none;
}

body {

  background-color: $body-bg;
  font-size: $font-size-base;
  font-weight: normal;
  min-height: 100%;
  overflow-x: hidden;
  position: relative;
}
::selection {
  background: $brand-secondary; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: $brand-secondary; /* Gecko Browsers */
}

p {
  font-weight: normal;
}

img {
  max-width: 100%;
}

strong {
  font-weight: 600;
}

ul {
  margin-bottom: 1em;
}

li {
  list-style: none;
  margin-bottom: .5em;
}

pre {
  margin-bottom: 2em !important;
}
.list-item{
  .header {
    position: relative;
   min-height: 3em;
   margin-top: 1em;
  h3 {

   position: absolute;
   bottom: 0;
   left: 0;
   }}
  .article {
    background-color: transparentize($white, .5);
    padding: 10px;
    border-left: 1.6em solid $brand-primary;
    text-align: justify;
  }
  margin-top:0.5em;
  .box-header {
  background: $brand-primary;
    color: $brand-light;
    font-size: 0.7em;
    padding: 2px;
    font-style: italic;



}
&:hover {
    transition: all .2s ease;
  .speech-bubble {
      transition: all .2s ease;
      background-color: $brand-secondary;
      &:after {
        border-left-color: $brand-secondary;
          transition: all .2s ease;
      }
  }

  .header + .box-header {
      transition: all .2s ease;
    background: $brand-secondary;

  }
  .article {
      transition: all .2s ease;
    border-color: $brand-secondary;
  }
  h3 {


    a{
      transition: all .2s ease;
    color:$brand-secondary; }
  }

}
transition: all 2s ease;

}
.list-item {
  display: inline-block;

  &.comment{
    background-color: fade-out($white, .7);
    padding: 1em;
    border: 1px solid fade-out($brand-primary, .7);

    .header {
      font-size: 0.9em;
      font-style: italic;
    }
  }

}
.list-enter-active, .list-leave-active {
  transition: all 0.2s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
